import { createActionHandlers, Document } from "@ali/4ever-cangjie";
import { TemplateButtonData } from '../../mo';

const TOGGLE_TEMPLATE_BUTTON_OPEN = Symbol('TOGGLE_TEMPLATE_BUTTON_OPEN');
const SET_TEMPLATE_BUTTON_DATA = Symbol('SET_TEMPLATE_BUTTON_DATA');
const INSERT_TEMPLATE_BUTTON = Symbol('INSERT_TEMPLATE_BUTTON');
const INSERT_FRAGMENT = Symbol('INSERT_FRAGMENT');
const DELETE_BUTTON = Symbol('DELETE_BUTTON');
const COPY_BUTTON = Symbol('COPY_BUTTON');
const CUT_BUTTON = Symbol('CUT_BUTTON');

export type SetDataProps = Partial<Omit<TemplateButtonData['metadata'], 'isOpen'>>;

const [
  insertTemplateButton, isInsertTemplateButton,
] = createActionHandlers<SetDataProps>(INSERT_TEMPLATE_BUTTON);

const [
  toggleTemplateButton, isToggleTemplateButton,
] = createActionHandlers<{ key: string }>(TOGGLE_TEMPLATE_BUTTON_OPEN);

const [
  setTemplateButton, isSetTemplateButton,
] = createActionHandlers<{ key: string, data: SetDataProps }>(SET_TEMPLATE_BUTTON_DATA);

interface InsertFragmentProps extends Required<Omit<SetDataProps, 'title'>>{
  fragment: Document;
}

const [
  insertTemplateButtonFragment, isInsertTemplateButtonFragment,
] = createActionHandlers<{ key: string, data: InsertFragmentProps }>(INSERT_FRAGMENT);

const [
  deleteTemplateButton, isDeleteTemplateButton,
] = createActionHandlers<{ key: string }>(DELETE_BUTTON);

const [
  copyTemplateButton, isCopyTemplateButton,
] = createActionHandlers<{ key: string }>(COPY_BUTTON);

const [
  cutTemplateButton, isCutTemplateButton,
] = createActionHandlers<{ key: string }>(CUT_BUTTON);


export const actions = {
  insertTemplateButton,
  toggleTemplateButton,
  setTemplateButton,
  insertTemplateButtonFragment,
  deleteTemplateButton,
  copyTemplateButton,
  cutTemplateButton,
}

export const isActions = {
  isInsertTemplateButton,
  isToggleTemplateButton,
  isSetTemplateButton,
  isInsertTemplateButtonFragment,
  isDeleteTemplateButton,
  isCopyTemplateButton,
  isCutTemplateButton,
}